import React, { Component } from "react";

export default class Resume extends Component {
  render() {
    let resumeData = this.props.resumeData;

    return (
      <section id="resume">
        {/* Education
          ----------------------------------------------- */}
        <div className="row education">
          <div className="three columns header-col">
            <h1>
              <span>Education</span>
            </h1>
          </div>
          <div className="nine columns main-col">
            {resumeData.education &&
              resumeData.education.map((item) => {
                return (
                  <div className="row item" key={item.UniversityName}>
                    <div className="twelve columns">
                      <h3>{item.universityName}</h3>
                      <p className="info">
                        {item.specialization}
                        <span>&bull;</span>{" "}
                        <em className="date">
                          {item.monthOfStarting} {item.yearOfStarting} -{" "}
                          {item.monthOfPassing} {item.yearOfPassing}
                        </em>
                      </p>
                      {item.relevantCourses !== "" ? (
                        <p>
                          <strong>Relavant courses:</strong>{" "}
                          {item.relevantCourses}
                        </p>
                      ) : null}
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
        <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Experience</span>
            </h1>
          </div>
          <div className="nine columns main-col">
            {resumeData.work &&
              resumeData.work.map((item) => {
                return (
                  <div className="row item" key={item.companyName}>
                    <div className="twelve columns">
                      <h3>{item.companyName}</h3>
                      <p className="info">
                        {item.specialization}
                        <span>&bull;</span>{" "}
                        <em className="date">
                          {item.monthOfStarting} {item.yearOfStarting} -{" "}
                          {item.monthOfLeaving} {item.yearOfLeaving}
                        </em>
                      </p>
                      <ul>
                        {item.workDone &&
                          item.workDone.map((item) => {
                            return (
                              <>
                                <li>{item}</li>
                              </>
                            );
                          })}
                      </ul>
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
        <div className="row skill">
          <div className="three columns header-col">
            <h1>
              <span>Skills</span>
            </h1>
          </div>
          <div className="nine columns main-col">
            <p>{resumeData.skillsDescription}</p>
            <div className="bars">
              <ul className="skills">
                {resumeData.skills &&
                  resumeData.skills.map((skill) => {
                    return (
                      <li>
                        <em>{skill.name}</em>
                      </li>
                    );
                  })}
              </ul>
            </div>
            {/* end skill-bars */}
          </div>{" "}
          {/* main-col end */}
        </div>{" "}
        {/* End skills */}
      </section>
    );
  }
}
